@font-face {
  font-family: Interstate-bold;
  src: url(../fonts/Interstate-bold.ttf);
}

@media (min-width: 700px) {
  .hitpoints_calculation {
    white-space: pre;
  }
}

@media (max-width: 700px) {

}

.hitpoints {
  display: grid;
  grid-template-rows: min-content 1fr;
  row-gap: 5px;
  align-items: center;
  border: 3px double black;
  border-radius: 2px 10px;
  width: min-content;
  margin: auto;
  padding: 3px;
}

.hitpoints.none {
  background-color: white;
}

.hitpoints_title {
  display: grid;
  grid-template-columns: min-content 1fr;
  column-gap: 3px;
  width: 100%;
  margin: auto;
  font-family: Interstate-bold;
  font-size: medium;
  white-space: nowrap;
}

.hitpoints_output {
  font-size: xx-large;
  width: min-content;
  margin: auto;
}

.hitpoints_calculation {
  text-align: center;
  font-size: x-small;
}